.tarea-caja { 
	width: 500px;
	min-height: 65px;
	background-color: white;
	margin: 5px 0;
	padding: 8px 15px 8px 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border: 1px solid darkolivegreen;
	cursor: pointer;
}

.tarea-texto {
	width: 100%;
	height: 100%;
	font-size: 20px;
	display: flex;
	align-items: center;
	overflow-wrap: anywhere;
}

.icono {
	width: 25px;
	height: 25px;
	margin: 5px;
}

/*Completo*/

.tarea-caja.completo {
	background-color: pink;
	text-decoration: line-through;
}